<style lang="postcss" scoped>
    .label-column {
        @apply text-white text-center text-sm
        border px-3 py-1 bg-blue-450
    }
    .hidden-mobile {
        @apply hidden sm:hidden md:block lg:block
    }
    .show-mobile {
        @apply block sm:hidden md:hidden lg:hidden
    }
    .data {
        @apply border px-2 py-1 text-xs
    }
</style>

<script>
import axios from 'axios';
import MainLayout from '@/pages/MainLayout';

    export default {
        data() {
            return {
                token: localStorage.getItem('token'),
                table: {},
                loading: false,
                request: {
                    sorted_by: "drawdown_desc",
                },
            }
        },
        components: {
            MainLayout,
        },
         mounted() {
            this.getLastData();
        },
        methods: {
            async getLastData() {
                let that = this;
                this.loading = true;

                await axios.post(process.env.VUE_APP_BASE_URL + "api/dashboard/last-data",
                            this.request,
                                {headers: { Authorization: `Bearer `+ that.token}})
                            .then(responses => {
                                let status = responses.data.status;
                                let data = responses.data.data;

                                if(status){
                                    this.table = data;
                                    this.loading = false;
                                }
                            })
                            .catch(error => {
                                console.log(error);
                            });
            },
            customEquity(number) {
                return Intl.NumberFormat('de-DE', { style: 'currency', currency: 'USC' }).format(number);
            },
        },
    }
</script>

